body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}


.navbar{
	width: 100%;position: relative;background-color: black;
	height: auto;padding: 30px;
}

.innavbar{position: relative;
	display: grid;grid-template-columns: 50% 50%;
	
}
a{
	text-decoration: none;
}
*{
	margin: 0;
	padding: 0;
}
.headings{
	display: -webkit-flex;
	display: flex;-webkit-flex-direction: row-reverse;flex-direction: row-reverse;grid-gap: 50px;font-size:17px;text-transform: uppercase;
}
.link, .contact{
	color: white;
}

.contact{
	border: 1px solid white;padding: 8px;
}
.link{
	padding: 8px;
}



