  width: 0px;
}
.triangle-gif{
	
	height: 80vh;
	background-image: url(/project/static/media/triangle.20f29d38.gif);
	width: 90%;margin: auto;
	background-size: contain;background-repeat: no-repeat;


}


.about{
	width: 100%;
	height:70vh;background: white;display: -webkit-flex;display: flex;-webkit-justify-content: center;justify-content: center;-webkit-align-items: center;align-items: center;

}
.in-about{
	width: 100%;height:auto;background-color:white;display: -webkit-flex;display: flex;-webkit-flex-direction: column;flex-direction: column;
	
}
.in-about .heading1{
	display: -webkit-flex;
	display: flex;-webkit-justify-content: center;justify-content: center;
}
.in-about .heading1 h1{font-weight: 100;
	color: red;background-color:white;text-transform: uppercase;font-size: 60px;font-family: 'Brygada';text-align: center;
}

.in-about .heading1 h2{font-weight: 100;
	color: black;text-transform: uppercase;font-size: 60px;font-family: 'Brygada';
}

.heading2{

}

.in-about .heading2 h1{
	text-align: center;font-family: 'Rock Salt',sans-serif;font-size: 35px;position: relative;left: 50px;top: -30px;
}
.in-about .heading3{
	width: 60%;
	margin: auto;
}
